
import Countup from 'vux/src/components/countup/index.vue'

import Group from '../components/User/Group'
import Cell from '../components/User/Cell'
import { mapActions, mapGetters } from 'vuex'
export default {
components: {
Countup,
Group,
Cell
},
created () {
if (Object.keys(this.$store.getters['user/userInfo']).length === 0) {
this.$vux.loading.show({
text: '正在加载'
})
this.getUserInfo().then((res) => {
this.$vux.loading.hide()
}).catch(() => {
this.$vux.toast.show({
text: '获取信息失败',
position: 'bottom',
type: 'text',
time: 1500,
width: '10em'
})
this.$vux.loading.hide()
})
}
},
computed: {
...mapGetters('user', [
'userInfo'
])
},
methods: {
...mapActions('user', [
'getUserInfo'
])
}
}



